#Header{
    height: 12vh;
    margin-bottom: 1vh;
}
#HeaderArea{
    display: flex;
    height: 100%;
    width: 70%;
    background-color: rgb(10, 10, 10);
}
#HeaderArea::after{
    content: '';
    position: absolute;
    left: 70%;
    height: 12vh;
    width: 15vw;
    background-color: rgb(10, 10, 10); 
    border-bottom-right-radius: 100%;

}
#WebName{
    cursor: default;
    align-self: center;
    margin-left: 5vw;
    font-size: 4vmin;
    font-family: var(--common-font);
    color: white;
}